import Wrapper from "../../../../common/core/Wrapper";
import React, {useContext, useState} from "react";
import {Text, RadioGroup, Radio} from 'informed';
import AutoForm from "../../../../common/core/AutoForm";
import s from "./s.module.scss";
import Button from "../../../../common/core/Button";
import useWaiting from "../../../../common/hooks/useWaiting";
import cn from 'classnames';
import DataContext from "../../../../contexts/DataContext";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import moment from "moment";

const Input = ({header, ...props}) => {
  return (
    <Col xs={6} lg={4} className={s.inputContainer}>
      <div className={s.inputHeader}>{header}</div>
      <Text className={cn(s.inputElement, s.shadow)} required {...props}/>
    </Col>
  );
};

const RadioItem = ({value, label}) => (
  <div className={s.radioItem}>
    <Radio className={s.radio} value={value}/>
    {label}
  </div>
);

const Profile = () => {
  const [formApi, setFormApi] = useState({});
  const {user} = useContext(DataContext);
  const {verified} = user.data;

  const submit = async (formState) => {
    await user.set(formState);
  };

  const [waiting, onSubmit] = useWaiting(submit);

  return (
    <Wrapper className={s.container}>
      <div className={s.status}>
        : <span className={cn(s.statusText, verified && s.verified)}>{
          verified ? '' : ''
        }</span>
      </div>
      <div className={s.editable}>
        <AutoForm
          onSubmit={onSubmit}
          formApi={formApi}
          setFormApi={setFormApi}
          autoComplete={'off'}
          initialValues={user.data}
        >
          <div className={s.header}> </div>
          <Row>
            <Input
              header={''}
              field={'firstName'}
            />
            <Input
              header={''}
              field={'secondName'}
            />
            <Input
              header={'Email'}
              field={'email'}
            />
            <Input
              header={' '}
              field={'birthDate'}
              parse={(value) => {
                return moment(value).format("YYYY-MM-DDTHH:00:00.000+00:00");
              }}
              format={(value) => moment(value).format("YYYY-MM-DD")}
              type={'date'}
            />
            <Input
              header={' '}
              field={'birthPlace'}
            />
            <Input
              header={' ()'}
              field={'citizenship'}
            />
          </Row>
          <div className={s.header}>  ()</div>
          <Row>
            <Input
              header={''}
              field={'country'}
            />
            <Input
              header={' '}
              field={'city'}
            />
            <Input
              header={''}
              field={'street'}
            />
            <Input
              header={', '}
              field={'building'}
            />
            <Input
              header={' ()'}
              field={'flat'}
            />
          </Row>
          <div className={s.header}></div>
          <RadioGroup field={'age'} required>
            <RadioItem value={'under20'} label={' 20 '}/>
            <RadioItem value={'from20to50'} label={' 20  50 '}/>
            <RadioItem value={'over50'} label={' 50 '}/>
          </RadioGroup>
          <div className={s.header}>
                     12 
          </div>
          <RadioGroup field={'earningMoney'} required>
            <RadioItem value={'yes'} label={'    '}/>
            <RadioItem value={'no'} label={'    '}/>
          </RadioGroup>
          <div className={s.header}>
            
          </div>
          <RadioGroup field={'savings'} required>
            <RadioItem value={'more'} label={'   ,    '}/>
            <RadioItem value={'notMore'} label={'    ,    '}/>
          </RadioGroup>
          <div className={s.header}> ()    </div>
          <RadioGroup field={'experience'} required>
            <RadioItem value={'from3'} label={' ( 3 )'}/>
            <RadioItem value={'from1to3'} label={' ( 1  3 )'}/>
            <RadioItem value={'under1'} label={' ( 1 )'}/>
            <RadioItem value={'none'} label={''}/>
          </RadioGroup>
          <div className={s.header}>  </div>
          <RadioGroup field={'purpose'} required>
            <RadioItem value={'protect'} label={'  ( )'}/>
            <RadioItem value={'smallProfit'} label={'  ( )'}/>
            <RadioItem value={'mediumProfit'} label={'   ( )'}/>
            <RadioItem value={'maxProfit'} label={'   ( )'}/>
          </RadioGroup>
          <Button
            containerClassName={s.buttonContainer}
            className={s.button}
            title={''}
            color={'white'}
            borderColor={'#42287D'}
            waiting={waiting}
            waitingColor={'rgba(255,255,255,0.35)'}
            backgroundColor={'#2C5BCA'}
          />
        </AutoForm>
      </div>
    </Wrapper>
  );
};

export default Profile;
