import React from 'react';
import styles from './styles.module.scss';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import Logo from "../Logo";

function Footer() {
  return (
    <div className={styles.container}>
      <Container className="flex-column">
        <Row>
          {addresses.map(Address)}
        </Row>
        <div className={styles.contact}>
          <Logo className={styles.logo}/>
          <Contacts/>
        </div>
        <div className={styles.subfooter}>
              FRONTIER MARKETS LTD   <strong><a href={require('../../../docs/certificate_fm.pdf')}>10912941</a></strong>     28 Monkville Avenue, London, United Kingdom     FCA 632380   
            Dragon Capital LTD,   <strong><a href={require('../../../docs/license.pdf')}>24303</a></strong>     Suite 305, Griffith Corporate Center, P.O. Box: 1510, Beachmont Kingstown, St. Vincent and Grenadines           .
        </div>
      </Container>
    </div>
  );
}

function Contacts() {
  return (
    <div className="d-flex align-items-center">
      <TouchableOpacity>
        <Image src={require('./images/telegram.png')} className={styles.icon}/>
      </TouchableOpacity>
      <TouchableOpacity>
        <Image src={require('./images/whatsapp.png')} className={styles.icon}/>
      </TouchableOpacity>
    </div>
  );
}

const addresses = [{
  title: '',
  address: '.  35, , ',
  telephone: '+7 499 681 34 18',
  email: 'support@fondru.com',
},{
  title: '',
  address: '8 Monkville Avenue, London,  United Kingdom',
  telephone: '+7 499 681 34 18',
  email: 'support@fondru.com',
},{
  title: '',
  address: '.  35, , ',
  telephone: '+7 499 681 34 18',
  email: 'support@fondru.com',
},];

function Address({title, address, telephone, email}) {
  return (
    <Col xs={12} lg={4} className={styles.address}>
      <div className={styles.title}>{title}</div>
      <div className={styles.info}>
        <Image src={require('./images/icon_place.png')}/>
        {address}
      </div>
      <div className={styles.info}>
        <Image src={require('./images/icon_phone.png')}/>
        {telephone}
      </div>
      <div className={styles.info}>
        <Image src={require('./images/icon_email.png')}/>
        {email}
      </div>
    </Col>
  );
}

export default Footer;
