import React from "react";
import {Col, Container} from "react-bootstrap";
import styles from './styles.module.scss';
import Image from "react-bootstrap/Image";
import Row from "react-bootstrap/Row";

const items = [{
  title: '',
  icon: require('./images/reputation.png'),
  text: '    ,      .',
},{
  title: '',
  icon: require('./images/reliability.png'),
  text: '      ,         .',
},{
  title: '',
  icon: require('./images/convenience.png'),
  text: '    .      .',
},{
  title: '',
  icon: require('./images/customers.png'),
  text: '         .',
},{
  title: '',
  icon: require('./images/partnership.png'),
  text: '    .    .'
},{
  title: '',
  icon: require('./images/development.png'),
  text: ' -     .',
}];

function Item({title, icon, text}) {
  return (
    <Col xs={12} sm={6} lg={4} className={styles.item}>
      <Image src={icon} className={styles.icon}/>
      <div className={styles.title}>{title}</div>
      <div className={styles.text}>{text}</div>
    </Col>
  );
}

function WhyUs() {
  return (
    <div className={styles.container}>
      <Container className="py-3">
        <Row>
          {items.map(Item)}
        </Row>
      </Container>
    </div>
  );
}

export default WhyUs;
